<template>
  <base-nav class="navbar-top navbar-dark" id="navbar-main" :show-toggle-button="false" expand>
    <ul class="navbar-nav align-items-center d-none d-md-flex ml-auto">
      <li class="nav-item">
        <div class="ml-2">
          <span
            class="mb-0 text-sm text-white font-weight-bold"
          >{{$store.state.user.firstName}} {{$store.state.user.lastName}}</span>
        </div>
      </li>
    </ul>
  </base-nav>
</template>
<script>
export default {
  data() {
    return {
      activeNotifications: false,
      showMenu: false,
      searchQuery: ""
    };
  },
  methods: {
    logout() {
      this.$store.dispatch("logout");
      setTimeout(() => this.$router.push({ name: "track" }), 100);
    },
    toggleSidebar() {
      this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
    },
    hideSidebar() {
      this.$sidebar.displaySidebar(false);
    },
    toggleMenu() {
      this.showMenu = !this.showMenu;
    }
  }
};
</script>
