import Vue from "vue";
import Vuex from "vuex";
import { login, track } from "./api/auth";
import { HTTP_OK } from "./utils/response-codes";
import { getPackages } from "./api/user";
// import createLogger from 'vuex/dist/logger';

const defaults = {
  user: {
    firstName: null,
    lastName: null,
    code: null,
    history: [{}],
    remember: false
  }
};

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: Object.assign({}, defaults.user),
    app: { name: '' },
    loading: true
  },
  mutations: {
    /**
     * Clears the auth user's auth data
     * @param state
     */
    clearAuth(state) {
      Vue.set(state, "user", defaults.user);
    },
    /**
     * Sets the auth user's data
     * @param state
     * @param payload
     */
    setAuthUser(state, user) {
      Vue.set(state, "user", user);
    },
    /**
     * Sets the apps data
     * @param state
     * @param payload
     */
    setAppData(state, appData) {
      Vue.set(state, "app", appData);
    },
    setLoading(state, loading) {
      Vue.set(state, 'loading', loading);
    },
  },
  actions: {
    /**
     * Authenticates a user's
     * @param context
     * @param credentials
     */
    login: async ({ commit }, { email, password, remember }) => {
      return await login(email, password, remember)
        .then(({ data, status }) => {
          const success = data && status === HTTP_OK;
          if (success) {
            commit("setAuthUser", data);
          }
          return success;
        })
        .catch(error => Promise.reject(error))
        .finally(() => { });
    },
    /**
     * Track packages by consinement
     * @param context
     * @param number
     */
    track: async ({ commit }, { code, remember }) => {
      return await track(code, remember)
        .then(({ data, success }) => {
          if (success) {
            commit("setAuthUser", data);
          }
          return success;
        })
        .catch(error => Promise.reject(error))
        .finally(() => { });
    },
    /**
     * Authenticates a user's
     * @param context
     * @param credentials
     */
    resume: async ({ commit }, { code }) => {
      return await getPackages(code).then(({ data, success }) => {
        if (success) {
          commit("setAuthUser", { ...data, remember: true });
        }
        return success;
      })
        .catch(error => Promise.reject(error))
        .finally(() => { });
    },
    logout: ({ commit }) => {
      commit("clearAuth");
    }
  },
  getters: {
    /**
     * Checks if app is (re-)loading mostly for the first time
     * @returns boolean
     */
    isLoading: ({ loading }) => loading,
    /**
     * Checks if a user is logged in
     * @returns boolean
     */
    isLoggedIn: ({ user: { code } }) => code && code.length == 23,
    /**
     * Returns the user
     * @returns string
     */
    authUser: ({ user }) => user,
    /**
     * Returns the app name
     * @returns string
     */
    appName: ({ app: { name } }) => name
  },
  strict: process.env.NODE_ENV !== "production" //,
  // plugins: [createLogger()]
});
